import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../components/Lo-gin.vue'
import Home from '../components/Ho-me.vue'
import Welcome from '../components/Wel-come.vue'
import Users from '../components/User/Us-ers.vue'
import Rights from '../components/Power/Ri-ghts.vue'
import Roles from '../components/Power/Ro-les.vue'
import Cate from '../components/goods/Ca-te.vue'
import Params from '../components/goods/Par-ams.vue'
import GoodsList from '../components/goods/Li-st.vue'
import AddGoods from '../components/goods/Add-goods.vue'
import Order from '../components/Order/Or-der.vue'
import Report from '../components/report/repo-rt.vue'
Vue.use(VueRouter)

const routes = [
  { path: '/', redirect: '/login' },
  {
    path: '/login',
    component: Login
  },
  {
    path: '/home',
    component: Home,
    redirect: '/welcome',
    children: [
      { path: '/welcome', component: Welcome },
      { path: '/users', component: Users },
      { path: '/rights', component: Rights },
      { path: '/roles', component: Roles },
      { path: '/categories', component: Cate },
      { path: '/params', component: Params },
      { path: '/goods', component: GoodsList },
      { path: '/goods/addgoods', component: AddGoods },
      { path: '/orders', component: Order },
      { path: '/reports', component: Report }
    ]
  }
]

const router = new VueRouter({
  routes
})
/* 挂载路由导航守卫 */
router.beforeEach((to, from, next) => {
  // to是将要访问的路径
  // from是从哪个路径跳转而来
  // next()是一个函数，代表放行 next('/login')代表强制跳转
  if (to.path === '/login') return next()/* 如果访问登录页直接放行 */
  // 获取token
  const tokenStr = window.sessionStorage.getItem('token')
  if (!tokenStr) return next('/login')/* 如果token为空 强制跳转 */
  next()/* 否则放行 */
})
export default router
