<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片区域 -->
    <el-card>
      <!-- 头部区域 -->
      <el-row :gutter="20">
        <!-- 搜索框区域 -->
        <el-col :span="10"
          ><el-input v-model="goodsListInfo.query" placeholder="请输入内容" clearable @clear="getGoodsList">
            <el-button
              @click="getGoodsList"
              slot="append"
              icon="el-icon-search"
            ></el-button> </el-input
        ></el-col>
        <!-- 添加商品按钮 -->
        <el-col :span="4">
          <el-button  type="primary" @click="goAddGoodsList">添加商品</el-button>
        </el-col>
      </el-row>
      <!-- 商品列表数据区域 -->
      <el-table  :data="goodsList" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="goods_name" label="商品名称"></el-table-column>
        <el-table-column prop="goods_price" label="商品价格(元)" width="95px"></el-table-column>
        <el-table-column prop="goods_weight" label="商品重量" width="70px"></el-table-column>
        <el-table-column prop="add_time" label="创建时间" width="140px">
            <template slot-scope="scope">
                {{scope.row.add_time | dateFormat}}
            </template>
        </el-table-column>
        <el-table-column label="操作" width="200px">
          <template slot-scope="scope">
            <el-button type="primary" icon="el-icon-edit" size="mini"
              >编辑</el-button
            >
            <el-button @click="removeGoods(scope.row.goods_id)" type="danger" icon="el-icon-delete" size="mini"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页区域 -->
      <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="goodsListInfo.pagenum"
      :page-sizes="[5, 10, 15, 20]"
      :page-size="goodsListInfo.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 商品列表数据数组
      goodsList: [],
      // 获取商品列表数据的请求参数
      goodsListInfo: {
        query: '',
        pagenum: 1, // 当前页码
        pagesize: 10 // 每页显示条数
      },
      // 商品数据总数
      total: 0
    }
  },
  created () {
    // 获取商品数据数组
    this.getGoodsList()
  },
  methods: {
    // 获取商品数据数组
    async getGoodsList () {
      const { data: res } = await this.$http.get('goods', {
        params: this.goodsListInfo
      })
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      this.goodsList = res.data.goods
      this.total = res.data.total
    },
    // 每页条数改变时的函数
    handleSizeChange (newSize) {
      this.goodsListInfo.pagesize = newSize
      this.getGoodsList()
    },
    // 当前页改变时的函数
    handleCurrentChange (newPage) {
      this.goodsListInfo.pagenum = newPage
      this.getGoodsList()
    },
    // 点击删除事件
    async  removeGoods (id) {
      const confirmResult = await this.$confirm('此操作将永久删除该商品, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('取消了删除')
      }
      const { data: res } = await this.$http.delete('goods/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      this.getGoodsList()
      this.$message.success(res.meta.msg)
    },
    // 添加商品按钮点击事件
    goAddGoodsList () {
      this.$router.push('/goods/addgoods')
    }
  }
}
</script>

<style lang="less" scoped>
.el-pagination{
    margin-left: 12.5%;
}
</style>
