<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>角色列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片视图 -->
    <el-card>
      <!-- 添加角色按钮区域 -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="addRoles">添加角色</el-button>
        </el-col>
      </el-row>
      <!-- 角色列表区域 -->
      <el-table :data="rolesList" border stripe>
        <!-- 展开列 -->
        <el-table-column type="expand">
          <template slot-scope="scoped">
            <el-row
              :class="[
                'dbbottom',
                i1 === 0 ? 'dbtop' : '',
                'vcenter',
                'vmargin',
              ]"
              v-for="(item1, i1) in scoped.row.children"
              :key="item1.id"
            >
              <!-- 渲染一级权限 -->
              <el-col :span="5">
                <el-tag @close="removeRightById(scoped.row, item1.id)" closable>
                  {{ item1.authName }}</el-tag
                >
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!-- 渲染二级和三级权限 -->
              <el-col :span="19">
                <!-- 通过for循环渲染二级权限 -->
                <el-row
                  :class="[i2 === 0 ? '' : 'dbtop', 'vcenter']"
                  v-for="(item2, i2) in item1.children"
                  :key="item2.id"
                >
                  <el-col :span="6">
                    <el-tag
                      @close="removeRightById(scoped.row, item2.id)"
                      closable
                      type="success"
                      >{{ item2.authName }}</el-tag
                    >
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <!-- 三级权限 -->
                  <el-col :span="18">
                    <el-tag
                      @close="removeRightById(scoped.row, item3.id)"
                      closable
                      type="warning"
                      v-for="item3 in item2.children"
                      :key="item3.id"
                      >{{ item3.authName }}</el-tag
                    >
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="角色名称" prop="roleName"></el-table-column>
        <el-table-column label="角色描述" prop="roleDesc"></el-table-column>
        <el-table-column label="操作" width="300px">
          <template slot-scope="scope">
            <el-button
              @click="editRoles(scope.row.id)"
              type="primary"
              icon="el-icon-edit"
              size="mini"
              >编辑</el-button
            >
            <el-button
              @click="deleteRoles(scope.row.id)"
              type="danger"
              icon="el-icon-delete"
              size="mini"
              >删除</el-button
            >
            <el-button
              @click="showSetRightDialog(scope.row)"
              type="warning"
              icon="el-icon-setting"
              size="mini"
              >分配权限</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <!-- 添加角色对话框 -->
      <el-dialog
        title="添加角色"
        :visible.sync="addRolesDialogVisible"
        width="50%"
        @close="addRolesClose"
      >
        <!-- 填写角色名称、描述的表单 -->
        <el-form
          :model="addRoledsForm"
          :rules="addRolesRules"
          ref="addRolesRef"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="角色名称" prop="roleName">
            <el-input v-model="addRoledsForm.roleName"></el-input>
          </el-form-item>
          <el-form-item label="角色描述" prop="roleDesc">
            <el-input v-model="addRoledsForm.roleDesc"></el-input>
          </el-form-item>
        </el-form>
        <!-- 底部区域 -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="addRolesDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="addRolesFn">确 定</el-button>
        </span>
      </el-dialog>
      <!-- 编辑角色对话框 -->
      <el-dialog
        title="修改角色"
        :visible.sync="editRolesDialogVisible"
        width="50%"
        @close="editRolesClose"
      >
        <!-- 编辑角色名称、描述的表单 -->
        <el-form
          :model="editRoledsForm"
          :rules="addRolesRules"
          ref="editRolesRef"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="角色名称" prop="roleName">
            <el-input v-model="editRoledsForm.roleName"></el-input>
          </el-form-item>
          <el-form-item label="角色描述" prop="roleDesc">
            <el-input v-model="editRoledsForm.roleDesc"></el-input>
          </el-form-item>
        </el-form>
        <!-- 底部区域 -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="editRolesDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="editRolesFn">确 定</el-button>
        </span>
      </el-dialog>
      <!-- 分配权限对话框 -->
      <el-dialog
        title="分配权限"
        :visible.sync="setRightDialogVisible"
        width="50%"
        @close="setRightDialogClosed"
      >
      <!-- 树形控件 -->
        <el-tree ref="treeRef" :data="rightsList" :props="treeProps" show-checkbox node-key="id" default-expand-all :default-checked-keys="defKeys"></el-tree>
        <span slot="footer" class="dialog-footer">
          <el-button @click="setRightDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="allRights"
            >确 定</el-button
          >
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 所以角色列表数据
      rolesList: [],
      // 所有权限列表数据
      rightsList: [],
      // 控制添加角色对话框的显示与隐藏
      addRolesDialogVisible: false,
      // 控制修改角色对话框的显示与隐藏
      editRolesDialogVisible: false,
      // 控制分配权限对话框的显示与隐藏
      setRightDialogVisible: false,
      // 添加角色输入框双向绑定的对象
      addRoledsForm: {
        roleName: '',
        roleDesc: ''
      },
      // 添加角色输入框的验证规则
      addRolesRules: {
        roleName: [
          { required: true, message: '请输入角色名称', trigger: 'blur' },
          {
            min: 3,
            max: 10,
            message: '长度在 3 到 10 个字符',
            trigger: 'blur'
          }
        ],
        roleDesc: [
          { required: true, message: '请输入角色描述', trigger: 'blur' },
          {
            min: 2,
            max: 15,
            message: '长度在 2 到 15 个字符',
            trigger: 'blur'
          }
        ]
      },
      // 根据id查到角色信息的对象
      editRoledsForm: {},
      // 树形控件的属性绑定事件
      treeProps: {
        children: 'children',
        label: 'authName'
      },
      // 树形控件默认选中节点id的数组
      defKeys: [],
      // 角色id
      roleId: ''
    }
  },
  created () {
    // 获取所以角色列表
    this.getRolesList()
  },
  methods: {
    // 调用角色列表接口
    async getRolesList () {
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) {
        return this.$message.error('获取角色列表失败')
      }
      this.rolesList = res.data
    },
    // 根据id删除对应权限
    async removeRightById (roles, rightId) {
      // 弹框提示用户是否删除
      const confirmResult = await this.$confirm(
        '此操作将永久删除该权限, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).catch((err) => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('用户取消了删除')
      }
      const { data: res } = await this.$http.delete(
        `roles/${roles.id}/rights/${rightId}`
      )
      if (res.meta.status !== 200) {
        return this.$message.error('删除权限失败')
      }
      roles.children = res.data
    },
    // 添加角色弹出对话框事件
    addRoles () {
      this.addRolesDialogVisible = true
    },
    // 编辑角色弹出对话框事件
    async editRoles (id) {
      const { data: res } = await this.$http.get('roles/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('查询失败')
      }
      this.editRoledsForm = res.data
      this.editRolesDialogVisible = true
    },
    // 监听添加角色对话框关闭后对表单进行重置
    addRolesClose () {
      this.$refs.addRolesRef.resetFields()
    },
    // 监听修改角色对话框关闭后对表单进行重置
    editRolesClose () {
      this.$refs.editRolesRef.resetFields()
    },
    // 添加角色对话框点击确定调用添加角色接口
    addRolesFn () {
      // 表单规则预验证
      this.$refs.addRolesRef.validate(async (valid) => {
        if (!valid) return
        // 可以调用添加角色接口
        const { data: res } = await this.$http.post(
          'roles',
          this.addRoledsForm
        )
        if (res.meta.status !== 201) {
          this.$message.error('添加角色失败')
        }
        this.addRolesDialogVisible = false
        this.getRolesList()
        this.$message.success('添加角色成功')
      })
    },
    // 编辑角色对话框点击确定调用编辑提交角色接口
    editRolesFn () {
      // 表单规则预验证
      this.$refs.editRolesRef.validate(async (valid) => {
        if (!valid) return
        // 可以调用编辑角色接口
        const { data: res } = await this.$http.put(
          'roles/' + this.editRoledsForm.id,
          {
            roleName: this.editRoledsForm.roleName,
            roleDesc: this.editRoledsForm.roleDesc
          }
        )
        if (res.meta.status !== 200) {
          return this.$message.error('编辑角色信息失败')
        }
        this.editRolesDialogVisible = false
        this.getRolesList()
        this.$message.success('编辑角色信息成功')
      })
    },
    // 点击删除按钮事件
    async deleteRoles (id) {
      const confirmResult = await this.$confirm(
        '此操作将永久删除该角色, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).catch((err) => err)
      // 点了取消
      if (confirmResult !== 'confirm') {
        return this.$message.info('已取消删除')
      }
      // 点了确定,调用删除接口
      const { data: res } = await this.$http.delete('roles/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('删除角色失败')
      }
      this.$message.success('删除角色成功')
      this.getRolesList()
    },
    // 点击分配权限弹出对话框
    async  showSetRightDialog (role) {
      // 获取角色id
      this.roleId = role.id
      // 获取所有权限数据
      const { data: res } = await this.$http.get('rights/tree')
      if (res.meta.status !== 200) {
        return this.$message.error('获取权限列表失败')
      }
      this.rightsList = res.data
      this.getLeafKeys(role, this.defKeys)
      this.setRightDialogVisible = true
    },
    // 通过递归的形式，获取角色下所有三级权限的id,并保存到defKeys数组中
    getLeafKeys (node, arr) {
      // 如果当前node节点不包含children属性，说明他是三级节点
      if (!node.children) {
        return arr.push(node.id)
      }
      node.children.forEach(item =>
        this.getLeafKeys(item, arr))
    },
    // 分配权限对话框关闭时把树形控件默认选中节点id的数组defKeys清空
    setRightDialogClosed () {
      this.defKeys = []
    },
    // 点击为角色分配权限
    async  allRights () {
      const keys = [
        ...this.$refs.treeRef.getCheckedKeys(), ...this.$refs.treeRef.getHalfCheckedKeys()
      ]
      const idStr = keys.join(',')
      const { data: res } = await this.$http.post(`roles/${this.roleId}/rights`, { rids: idStr })
      if (res.meta.status !== 200) {
        return this.$message.error('分配权限失败')
      }
      this.$message.success('分配权限成功')
      this.getRolesList()
      this.setRightDialogVisible = false
    }
  }
}
</script>

<style lang="less" scoped>
.el-tag {
  margin: 7px;
}
.dbtop {
  border-top: 1px solid #eee;
}
.dbbottom {
  border-bottom: 1px solid #eee;
}
.vmargin {
  margin-left: 40px;
}
.vcenter {
  display: flex;
  align-items: center;
}
</style>
